import { useIdentity } from "../hooks/identity";
import { useState } from "react";
import s from "../style/form.module.sass"

export function PostForm (props) {
    var [id, axios] = useIdentity();
    var activeUser = id.users[id.active];

    var [title, setTitle] = useState("");
    var [text, setText] = useState("");

    function change(evt) {
        switch(evt.target.name) {
            case "title":
                return setTitle(evt.target.value);
            case "text":
                return setText(evt.target.value);
        }
    }

    function submit(evt) {
        console.log("Title:", title);
        console.log("Text:", text);
        evt.preventDefault();
    }

    return (
        <form onSubmit={submit} className={s.form}>
            <input 
                type="text" 
                value={title}
                onChange={change}
                name="title" 
                placeholder="The post's title" 
                autoComplete="off"
            />
            <textarea 
                name="text" 
                value={text}
                onChange={change}
                placeholder="Go ahead, unleash your creative Genius!" 
                rows="4" 
                autoComplete="off"
            ></textarea>
            <input 
                type="submit" 
                value="Post"
                data-anonymous={!Boolean(activeUser)} 
            />
            <div>
                as
                <span>{activeUser ? activeUser.username : "<UNKNOWN>"}</span>
            </div>
            
        </form>
    );
}
